.flex {
  display: flex;
}

.flex-aligncenter {
  display: flex;
  align-items: center;
}

.flex-justifycenter {
  display: flex;
  justify-content: center;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-Container {
  background-color: #F1F1F1;
  height: 100vh;
}

.userManagementContainer {
  .MuiFormControl-root {
    height: 32px;
    width: 152px;
    background: white;
    border-radius: 20px;
    border-color: #D8D8D8;
    margin-right: 16px;

    .MuiInputBase-root {
      height: 100%;
      width: 100%;
      border-radius: 20px;

      .MuiInputBase-input {
        height: 100%;
        width: 100%;
        font-size: 12px !important;
        color: #565656;
        padding: 8px 14px !important;
      }
    }
  }

  .tablepagination {
    .textFieldSelect {
      .MuiFormControl-root {
        height: 29.13px;
        width: 48px;
        border-radius: 0;
        border: none;
        outline: none;
        background: transparent;
      }

      .MuiInputBase-root {
        border-radius: 0 !important;
        border: none;
        outline: none;
        background: transparent;
      }

      .MuiFormControl-root .MuiInputBase-root .MuiInputBase-input {
        display: flex;
        align-items: center;
        padding: 0 5px 0 10px !important;
        outline: none;
        border: none;
        font-size: 14px !important;
        font-family: 'Noto Sans' !important;
        font-weight: bold !important;
        color: #474747;
      }

      .MuiSvgIcon-root {
        transform: scale(0.778) !important;
        color: #C3C3C3 !important;
      }

      .MuiOutlinedInput-notchedOutline {
        outline: none;
        border: none;
        appearance: none;
      }
    }
  }

  .MuiTablePagination-root {
    .MuiToolbar-root {
      height: inherit !important;
      min-height: inherit !important;
      border: 1px solid #C3C3C3 !important;
      padding: 0 0px !important;
      border-radius: 4px;

      .MuiTablePagination-selectLabel {
        display: none;
      }

      .MuiInputBase-root {
        margin: 0 5px 0 0 !important;
      }

      .MuiTablePagination-displayedRows {
        display: none;
        margin: 0 !important;
      }

      .MuiBox-root {
        align-items: center;
        display: flex;
        padding: 8px 8px 8px 0 !important;
        margin-left: 10px !important;

        .MuiButtonBase-root {
          padding: 0 !important;

          .MuiSvgIcon-root {
            transform: scale(1.2) !important;
          }
        }
      }
    }
  }
}

.mainUserTableHead {
  border-radius: 4px 4px 0px 0px;
  height: 48px !important;

  .MuiSvgIcon-root {
    transform: scale(0.889) !important;
  }

  .MuiTableCell-root {
    padding: 0;
  }

  .MuiCheckbox-root {
    padding: 9px 11px !important;
  }
}

.mainUserBodyTable {
  background: #FFF;

  .MuiTableCell-root {
    padding: 0;
  }

  .MuiTableRow-root {
    height: 48px !important;
  }

  .tablecellonHover {
    .MuiSvgIcon-root {
      transform: scale(0.889) !important;
    }
  }
}

